// IMPORTS
// Material UI
import AppBar from '@material-ui/core/AppBar'
import Dialog from '@material-ui/core/Dialog'
import IconButton from '@material-ui/core/IconButton'
import Slide from '@material-ui/core/Slide'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import CloseIcon from '@material-ui/icons/Close'

// Externals imports (Dependencies)
import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

// Internal imports
import styles from '../../../resources/theme/default'
import EnhancedComponent from '../../components/EnhancedComponent'
import { destroy, edit, fetchStores, update } from '../StoreActions'
import { getEditingStore } from '../StoreReducer'
import Form from './Form'

function Transition(props) {
  return <Slide direction="down" {...props} />
}

class DestroyStore extends EnhancedComponent {
  state = {
    fields: {
      id: '',
      name: '',
      active_points: '',
      responsable_name: '',
      responsable_email: '',
      points: '',
      active_ponts: '',
      accumulation_points: '',
    },
  }

  onSubmit = values => {
    const { fetchCompanies } = this.props
    // valida os campos se necessÃ¡rio
    this.props.destroy(values.id).then(data => {
        fetchStores()
      if (data && data.code === 200) {
        this.props.history.push('/stores')
      }
    })
  }

  componentDidMount() {
    this.props.edit(this.props.match.params.id)
  }

  onClose = () => {
    this.props.history.push('/stores')
  }

  render() {
    const { classes, company } = this.props
    return (
      <Dialog
        open={company !== null}
        onClose={this.onClose}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <Typography variant="h6" color="inherit" className={classes.flex}>
              Deletar Lojista
            </Typography>
            <IconButton
              color="inherit"
              onClick={this.onClose}
              aria-label="Close"
            >
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>
        <Form data={company} onClose={this.onClose} onSubmit={this.onSubmit} />
      </Dialog>
    )
  }
}

const mapStateToProps = state => ({
  /* roles: state.user.roles || [], */
  company: getEditingStore(state),
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      update,
      edit,
      destroy,
      fetchStores,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(DestroyStore))